
.modal {
    width:100%;height: 100%;position: fixed;left:0;top: 0;overflow: hidden;
    z-index:99999;background: rgba(0,0,0,.5);
}
.modal .icon {
    width:.6rem;
    height:.6rem;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.modal button {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.modal .dialog {
    background: #FFFFFF;
    min-width: 4.5rem;
    padding: .3rem .2rem;
    text-align: center;
    border-radius: .1rem;
    box-shadow: 0rem .1rem .3rem 0rem rgba(0, 0, 0, .1);
    position: fixed;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%)
}

.modal .dialog>h4 {
    color: #454545;
    text-align: center;
    margin: 0;
    font-size: .4rem;
    font-weight:normal
}

.modal .dialog .button-wrapper {
    padding: .3rem .6rem 0;
    display: flex;
    justify-content: space-around;
}

.modal .dialog .button-wrapper>button>svg {
    font-size: .6rem;
}

.modal .dialog .button-wrapper .check>svg {
    color: #50C881;
}

.modal .dialog .button-wrapper .close>svg {
    color: #FF5152;
}

.modal-alert>button {
    font-size: .6rem;
    color: #50C881;
    margin-top: .3rem;
}

.modal-prompt>input[type=text] {
    font-size: .2rem;
    margin: .2rem .5rem 0;
    padding: .1rem .3rem;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, .02);
    border-radius: .1rem;
}

.dialog-wrapper-enter {
    opacity: 0.01;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
}

.dialog-wrapper-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}

.dialog-wrapper-exit {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.dialog-wrapper-exit-active {
    opacity: 0.01;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}

.modal-wrapper-enter {
    background: rgba(0, 0, 0, 0);
}

.modal-wrapper-enter-active {
    background: rgba(0, 0, 0, .4);
    -webkit-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
}

.modal-wrapper-exit {
    background: rgba(0, 0, 0, .4);
}

.modal-wrapper-exit-active {
    background: rgba(0, 0, 0, 0);
    -webkit-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
}
.icon {
    width:.6rem;
    height:.6rem;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.toast-notification {
    position: fixed;
    top: 50%;
    left:50%;
    display: flex;
    flex-direction: column;
    z-index:10000;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%)
}

.toast-notice-wrapper.notice-enter {
    opacity:0.01;
    -webkit-transform:scale(0);
            transform:scale(0);
}
.toast-notice-wrapper.notice-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}
.toast-notice-wrapper .notice-exit {
    opacity: 1;
    -webkit-transform: translateY(1);
            transform: translateY(1);
}

.toast-notice-wrapper.notice-exit-active{
    opacity: 0.01;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}
.toast-notice {
    background: #FFFFFF;
    padding: .2rem .4rem;
    margin: .1rem auto;
    box-shadow: 0rem .1rem .3rem 0rem rgba(0, 0, 0, .1);
    border-radius: .1rem;
    color: #454545;
    font-size: .4rem;
    display: flex;
    align-items: center;
}

.toast-notice>span {
    margin-left: .1rem;
    line-height: 100%;
}

.toast-notice>svg {
    font-size: .2rem;
}

.toast-notice.info>svg {
    color: #1890FF;
}

.toast-notice.success>svg {
    color: #52C41A;
}

.toast-notice.warning>svg {
    color: #FAAD14;
}

.toast-notice.error>svg {
    color: #F74A53;
}

.toast-notice.loading>svg {
    color: #1890FF;
    -webkit-animation: rotating 1s linear infinite;
            animation: rotating 1s linear infinite;

}

@-webkit-keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
